<template>
    <v-card>
        <v-snackbar v-model="show"
                :bottom="y === 'bottom'"
                :left="x === 'left'"
                :multi-line="mode === 'multi-line'"
                :right="x === 'right'"
                :timeout="timeout"
                :top="y === 'top'"
                :vertical="mode === 'vertical'">
                {{ value }}
            <v-btn color="green" flat @click="show = false">
                Fechar
            </v-btn>
        </v-snackbar>
    </v-card>
</template>

<script>
    export default {
        name: "NotifyComponent",
        data () {
            return {
               value : '',
            }
        },
        props:{
            show :{ type: Boolean, required: true },
            y :{ type: String, required: true },
            x :{ type: String, required: true },
            mode :{ type: String, required: true },
            timeout :{ type: Number, required: true },
            text :{ type: String, required: true },
            type :{ type: String, required: true },
        },
        methods:{
            messager(error){
                try{

                    for(let item in error.response.data.errors)
                    {
                        this.value += error.response.data.errors[item][0];
                    }

                }catch (e) {
                    this.value = 'REQUEST ERRO'
                }
            }
        },
        watch:{
            show(){
                this.value = '';
                if(this.type === 'error'){
                    this.messager(this.text);
                }else {
                    this.value = this.text;
                }
            }
        }
    }
</script>

