<template>
   <div style="width: 100%">
       <v-toolbar dark color="teal lighten-2">
           <v-toolbar-title class="white--text">Importação</v-toolbar-title>
           <v-spacer></v-spacer>

           <v-toolbar-title class="white--text" style="font-size: 12px; cursor: pointer"  @click="btnDownload">Download planilha padrão</v-toolbar-title>
           <v-btn icon @click="btnDownload">
               <v-icon>arrow_downward</v-icon>
           </v-btn>
       </v-toolbar>

       <v-flex>
           <div style="width: 100%">
               <v-text-field label="Selecione uma planilha" @click='pickFile' v-model='fileName' prepend-icon='attach_file' style="float: left; width: 600px">
               </v-text-field>
               <v-spacer></v-spacer>
               <v-btn @click.prevent="pickFile" style="float: left" color="info">Selecionar</v-btn>

               <v-spacer></v-spacer>
               <input ref="fileUpload" name="fileUpload" type="file" accept=".xlsx" style="display: none" @change="onFilePicked">
               <v-btn @click.prevent="sendUpload" color="success">Importar</v-btn>
           </div>

       </v-flex>

       <br>

       <v-data-table
               v-model="selected"
               :headers="headersTable"
               :items="desserts"
               :pagination.sync="pagination"
               select-all
               item-key="id"
               class="elevation-1"
               :loading="loading"
               no-data-text="Nenhum produto encontrato">
           <template v-slot:headers="props">
               <tr>
                   <th>
                       <v-checkbox :input-value="props.all"
                                   :indeterminate="props.indeterminate"
                                   primary
                                   hide-details
                                   @click.stop="toggleAll">
                       </v-checkbox>
                   </th>
                   <th v-for="header in props.headers"
                       class="text-xs-left"
                       :key="header.text"
                       :class="['column sortable', pagination.descending ? 'desc' : 'asc', header.value === pagination.sortBy ? 'active' : '']"
                       @click="changeSort(header.value)">
                       <v-icon small>arrow_upward</v-icon>
                       {{ header.text }}
                   </th>
               </tr>
           </template>
           <template v-slot:items="props">
               <tr>
                   <td>
                       <v-checkbox :input-value="props.selected" primary hide-details></v-checkbox>
                   </td>
                   <td class="text-xs-center">
                       {{ props.item.id }}
                   </td>
                   <td class="text-xs-center" nowrap>{{ props.item.queue.descricao }}</td>
                   <td class="text-xs-center" nowrap>{{ props.item.queue.file_name }}</td>
                   <td class="text-xs-center" nowrap>{{ props.item.created_at }}</td>
                   <td class="text-xs-center" nowrap>
                       <v-btn :color="props.item.situacao.class_button" small @click="btnDownloadFile(props.item.situacao.id, props.item.id)">{{props.item.situacao.name}}</v-btn>
                   </td>

               </tr>
           </template>
       </v-data-table>

       <notify-component
       :show="notify.show"
       :y="notify.y"
       :x="notify.x"
       :mode="notify.mode"
       :timeout="notify.timeout"
       :text="notify.text"
       :type="notify.type"/>
   </div>
</template>

<script>
    import NotifyComponent from '../Notifictions/NotifyComponent'
    export default {
        name: "IndexImportacaoComponent",
        components:{NotifyComponent},
        data() {
            return {

                loading         : false,
                btnLoadin       : false,
                tipoAgenda      : [],
                msgError        : "",
                headers         : axios.defaults.headers.common,
                file            : '',
                fileName        : '',
                idTipoAgenda    : '',
                uploadPorcent   : 0,
                dialog: false,
                fileUrl: '',
                produtos: [],

                selected : [],
                headersTable: [
                    { text: 'ID', value: 'ID'},
                    { text: 'DESCRICAO', value: 'DESCRICAO'},
                    { text: 'NOME DO AQUIVO', value: 'NOME DO AQUIVO'},
                    { text: 'DATA', value: 'DATA'},
                ],
                desserts: [],

                notify:{
                    show: false,
                    y: 'top',
                    x: '',
                    mode: '',
                    timeout: 6000,
                    text: '',
                    type : ''
                }
            }
        },
        methods:{
            pickFile () {
                this.$refs.fileUpload.click ()
            },
            onFilePicked (e) {
                const files = e.target.files
                if(files[0] !== undefined) {
                    this.fileName = files[0].name
                    if(this.fileName.lastIndexOf('.') <= 0) {
                        return
                    }
                    const fr = new FileReader ()
                    fr.readAsDataURL(files[0])
                    fr.addEventListener('load', () => {
                        this.fileUrl = fr.result
                        this.file = files[0] // this is an image file that can be sent to server...
                    })
                } else {
                    this.fileName = ''
                    this.file = ''
                    this.fileUrl = ''
                }
            },
            btnDownload()
            {
                axios({
                    method      :'post',
                    url         :'download-planilha-padrao',
                    responseType:'arraybuffer',
                })
                    .then(function(response)
                    {
                        this.loading = false;
                        this.$forceDownload(response,'consultaPreco.xlsx');
                    }.bind(this))
                    .catch(function (resp) {
                        //this.loading = false;
                    }).then(function () {
                   // this.loading = false;
                }.bind(this));
            },


            btnDownloadFile(id_situacao, id)
            {

                if(id_situacao !== 2)
                {
                    return;
                }

                this.loading = true;
                axios({
                    method      :'post',
                    url         :'consulta-download',
                    responseType:'arraybuffer',
                    data        : {idFile : id}
                })
                .then(function(response)
                {
                    this.loading = false;
                    this.$forceDownload(response,'consulta-'+id+'.csv');
                }.bind(this))
                .catch(function () {
                    this.loading = false;
                }).then(function () {
                this.loading = false;
                }.bind(this));
            },
            listarDownloads()
            {
                this.btnLoadin = true;
                axios.post('download-listar').then(function (result)
                {
                    this.desserts = result.data;
                    this.btnLoadin = false;
                }.bind(this))
                .catch(function (error)
                {
                    this.btnLoadin = false;
                }.bind(this));

            },

            sendUpload(){
                let formData = new FormData();
                formData.append('fileUpload',this.file);
                axios.post('importar', formData,{headers: {
                    'Content-Type': 'multipart/form-data'
                }})
                    .then(function (response)
                    {
                        this.produtos = response.data.matriz;
                        this.notify.show = false;
                        this.Queues();
                    }.bind(this))
                    .catch(function (error)
                    {
                        this.notify.show = true;
                        this.notify.y = 'top';
                        this.notify.x = 'right';
                        this.notify.mode = 'multi-line';
                        this.notify.text =  error;
                        this.notify.type =  'error';
                    }.bind(this))
                    .then(function ()
                    {
                    }.bind(this))
            },

            toggleAll () {
                if (this.selected.length){
                    this.selected = []
                }
                else{
                    this.selected = this.desserts.slice()
                }
            },
            changeSort (column) {
                if (this.pagination.sortBy === column) {
                    this.pagination.descending = !this.pagination.descending
                } else {
                    this.pagination.sortBy = column
                    this.pagination.descending = false
                }
            },
            Queues(){
                this.listarDownloads();

                setInterval(function ()
                {
                    this.listarDownloads();
                    this.desserts.forEach(function (data) {

                        if(data.situacao.id === 1){
                            return;
                        }
                    });
                }.bind(this), 10000)
            },
        },
        mounted() {

        }
    }
</script>

<style scoped>

</style>