<template>

    <div  style="width: 100%">

        <v-toolbar dark color="teal lighten-2">
            <v-toolbar-title class="white--text">Gerar Download</v-toolbar-title>
            <v-btn icon @click="btnDownloadArquivo">
                <v-icon>arrow_downward</v-icon>
            </v-btn>
            <v-btn icon @click="listarDownloads">
                <v-icon>refresh</v-icon>
            </v-btn>
        </v-toolbar>


        <v-data-table
                v-model="selected"
                :headers="headers"
                :items="desserts"
                :pagination.sync="pagination"
                select-all
                item-key="id"
                class="elevation-1"
                :loading="loading"
                no-data-text="Nenhum produto encontrato">
            <template v-slot:headers="props">
                <tr>
                    <th>
                        <v-checkbox :input-value="props.all"
                                    :indeterminate="props.indeterminate"
                                    primary
                                    hide-details
                                    @click.stop="toggleAll">
                        </v-checkbox>
                    </th>
                    <th v-for="header in props.headers"
                        class="text-xs-left"
                        :key="header.text"
                        :class="['column sortable', pagination.descending ? 'desc' : 'asc', header.value === pagination.sortBy ? 'active' : '']"
                        @click="changeSort(header.value)">
                        <v-icon small>arrow_upward</v-icon>
                        {{ header.text }}
                    </th>
                </tr>
            </template>
            <template v-slot:items="props">
                <tr>
                    <td>
                        <v-checkbox :input-value="props.selected" primary hide-details></v-checkbox>
                    </td>
                    <td class="text-xs-center">
                        {{ props.item.id }}
                    </td>
                    <td class="text-xs-center" nowrap>{{ props.item.descricao }}</td>
                    <td class="text-xs-center" nowrap>{{ props.item.file_name }}</td>
                    <td class="text-xs-center" nowrap>{{ props.item.created_at }}</td>
                    <td class="text-xs-center" nowrap>
                        <v-btn color="success" @click="btnDownload(props.item.id)">Download</v-btn>
                    </td>

                </tr>
            </template>
        </v-data-table>

        <v-dialog v-model="loading"
                  hide-overlay
                  persistent
                  width="300">
            <v-card color="primary" dark>
                <v-card-text>
                    Por favor aguarde...
                    <v-progress-linear
                            indeterminate
                            color="white"
                            class="mb-0">
                    </v-progress-linear>
                </v-card-text>
            </v-card>
        </v-dialog>
    </div>
</template>

<script>
    export default {
        name: "DownloadConsultasComponent",
        data() {
            return {
                alerta : {
                    value : false,
                    msg : 'Error',
                    color : 'error',
                    icon : 'warning'
                },
                loading : false,
                btnLoadin  : false,
                selected : [],
                headers: [
                    { text: 'ID', value: 'ID'},
                    { text: 'DESCRICAO', value: 'DESCRICAO'},
                    { text: 'NOME DO AQUIVO', value: 'NOME DO AQUIVO'},
                    { text: 'DATA', value: 'DATA'},
                ],
                desserts: []
            }
        },
        methods:{

            btnDownload(id)
            {
                this.loading = true;
                axios({
                    method      :'post',
                    url         :'consulta-download',
                    responseType:'arraybuffer',
                    data        : {idFile : id}
                })
                    .then(function(response)
                    {
                        this.loading = false;
                        this.$forceDownload(response,'consulta-'+id+'.csv');
                    }.bind(this))
                    .catch(function () {
                        this.loading = false;
                    }).then(function () {
                    this.loading = false;
                }.bind(this));
            },

            btnDownloadArquivo()
            {
                this.loading = true;
                this.btnLoadin = true;
                axios.post('gerar-download').then(function (result)
                {
                    this.btnLoadin = false;
                    this.loading = false;
                    this.listarDownloads();
                }.bind(this))
                    .catch(function (error)
                    {
                        this.btnLoadin = false;
                        this.loading = false;
                    }.bind(this));

            },
            listarDownloads()
            {
                this.btnLoadin = true;
                axios.post('download-listar').then(function (result)
                {
                    this.desserts = result.data;
                    this.btnLoadin = false;
                }.bind(this))
                    .catch(function (error)
                    {
                        this.btnLoadin = false;
                    }.bind(this));

            },
            toggleAll () {
                if (this.selected.length){
                    this.selected = []
                }
                else{
                    this.selected = this.desserts.slice()
                }
            },
            changeSort (column) {
                if (this.pagination.sortBy === column) {
                    this.pagination.descending = !this.pagination.descending
                } else {
                    this.pagination.sortBy = column
                    this.pagination.descending = false
                }
            }
        },
        mounted(){
            this.listarDownloads();
        }
    }
</script>

<style scoped>

</style>