<template>
    <div>
        <v-dialog v-model="showDialog" persistent max-width="800px">
            <v-card>
                <v-card-title class="grey lighten-4 py-4 title">
                   Adicionar Filtros
                </v-card-title>
                <v-container grid-list-sm class="pa-4">
                    <v-layout row wrap>
                        <v-flex xs12>
                            <v-text-field v-model="form.name" prepend-icon="place" placeholder="Filtro"></v-text-field>
                        </v-flex>
                    </v-layout>
                </v-container>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn flat color="error" @click="showDialog = false">Cancelar</v-btn>
                    <v-btn flat  color="success" @click="submit">Salvar</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>

        <notify-component
                :show="notify.show"
                :y="notify.y"
                :x="notify.x"
                :mode="notify.mode"
                :timeout="notify.timeout"
                :text="notify.text"
                :type="notify.type"/>
    </div>
</template>

<script>
    import NotifyComponent from '../Notifictions/NotifyComponent'
    export default {
        name: "IndexConfiguracaoComponent",
        components:{NotifyComponent},
        data: () => ({
            form :{
                name: '',
            },
            notify:{
                show: false,
                y: 'top',
                x: '',
                mode: '',
                timeout: 6000,
                text: '',
                type: ''
            },
            showDialog: false,
        }),
        props: {
            dialog: Boolean
        },
        methods:{
          submit(){
              axios.post('filtros', this.form).then(function (result)
              {
                  this.notify.show = true;
                  this.notify.y = 'top';
                  this.notify.x = 'right';
                  this.notify.mode = 'multi-line';
                  this.notify.text =  "Cadastro efetuado com sucesso!";
                  this.notify.type =  'sucesso';
                  this.showDialog = false;
              }.bind(this))
                  .catch(function (error)
                  {
                      this.notify.show = true;
                      this.notify.y = 'top';
                      this.notify.x = 'right';
                      this.notify.mode = 'multi-line';
                      this.notify.text =  error;
                      this.notify.type =  'error';
                   }.bind(this));

          },
        },
        watch:{
            dialog(){
                this.showDialog = this.dialog;
            }
        }
    }
</script>

